<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div class="btn-main-deepen"
             @click="$router.push(`/student-work/activity/publicity`)">
          发布活动
        </div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>

      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="35%">标题</th>
          <th width="15%">发布时间</th>
          <th width="25%">范围</th>
          <th width="15%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.title}}</td>
          <td>{{item.createdTime | substrTime }}</td>
          <td><span v-for="scopeItem in item.scopeList"> {{scopeItem.scopeName}}</span></td>
          <td>
            <span class="oper"
                  @click="$router.push(`/student-work/activity/id?id=`+item.id+``)">查看</span>
            <span @click="$router.push(`/student-work/activity/publicity?id=`+item.id+``)"
                  class="oper">编辑</span>
            <span @click="remove(item)" class="oper">删除</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        nav: [{name: '活动管理', path: ''}],
        searchData: [{type: 'input', placeholder: '标题'}],
        table: {
          url: '/notice/queryNoticePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            noticeType: "ACTIVITY",
            keywords: null
          }
        },
        dialogVisible: false,
        title: null,
        switch: false,
        switchOne: true,
      }
    },
    methods: {
      // 删除活动
      async remove (item) {
        await this.$confirm(`您确认删除【${item.title}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/notice/deleteNotice.do',
          query: {
            id: item.id,
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      }
    },
    async mounted () {
      this.$search(this.table)
    }
  }
</script>
